import React, { Component } from 'react'
import Table from './Table'
import Formulaire from './Formulaire'
import User from './User'
import Popup from './Popup'

/*
 * App definition
 * Centralize the app
 */


class App extends Component {
    state = {
        characters: [],
        showPopup: false , 
    }
    removeCharacter = (index) => {
        const { characters } = this.state

        this.setState({
            characters: characters.filter((character, i) => {
                return i !== index
            }),
        })
    }
    
    togglePopup() {
        this.setState({
            showPopup: !this.state.showPopup
        });
    }  

    handleSubmit = (character) => {
        this.setState({ characters: [...this.state.characters, character] })
    }
    sendInfo = (characters) => {

        const seedsValue = [...this.state.characters]
        if (seedsValue != null) {
            for (var i = 0, l = seedsValue.length; i < l; i++) {
                if (seedsValue[i].seedingOutdoor !== "") {
                    seedsValue[i].seedingOutdoor = seedsValue[i].seedingOutdoor.map((month) => (month.value))
                }
                if (seedsValue[i].seedingIndoor !== "") {
                    seedsValue[i].seedingIndoor = seedsValue[i].seedingIndoor.map((month) => (month.value))
                }
                if (seedsValue[i].harvest !== "") {
                    seedsValue[i].harvest = seedsValue[i].harvest.map((month) => (month.value))
                }
            }

        }
  

        this.setState({
            showPopup: true,
            back: {
                email: characters.email,
                name: characters.name,
                info: "",
                seeds: seedsValue
            }
        })

        fetch('https://chefphan.com/gh-api/', {
                method: 'POST',
                body: JSON.stringify({
                    email: characters.email,
                    name: characters.name,
                    info: "",
                    seeds: seedsValue
                }),
                headers: { 'Content-Type': 'application/json' }
        })
                .then(res => res.json())
                .catch(error => console.error('Error:', error))
                .then(response => console.log('Success:', response));
    }
    render() {
        const { characters } = this.state

        return (
            <div className="container">
                <User sendInfo={this.sendInfo} />
                {this.state.showPopup ?
                    <Popup
                        text={this.state.back}
                        closePopup={this.togglePopup.bind(this)}
                    />  
                    : null
                     } 
                <Table characterData={characters} removeCharacter={this.removeCharacter} />
                <Formulaire handleSubmit={this.handleSubmit} />
            </div>
        )
    }
}

export default App
